<template>
    <div>
        <input v-model="sousou" placeholder="输入歌曲名字/歌手/歌词" @change="slgedan"  style="width: 60%;margin-top: 20px;margin-bottom: 10px;height: 20px">
        <ul class="gedan">
            <li v-for="song in ret" :key="song.id" class="gelan">
                <div class="dtimg"  @click="bofang(song)">
                    <img :src="song.picUrl" alt="" style="width:100px;border-radius: 5px">
                    <div class="el-icon-video-play but">
                    </div>
                </div>

                    <span  class="mname">{{song.name}}</span>

            </li>
        </ul>
    </div>
</template>

<script>
    export default {
        name: "diantai",
        data(){
            return{
                sousou:"",
                ret:[]
            }
        },
        methods: {
            slgedan() {
                this.$axios.get("https://api.imjad.cn/cloudmusic/", {
                    params: {
                        type: "search",
                        s: this.sousou,
                        search_type: 1009
                    }
                }).then(res => {
                    // console.log(res)
                    if (!res.data.result) {
                        alert("搜索结果为空！")
                    } else {
                        this.ret = res.data.result.djRadios
                        console.log(this.ret)
                    }
                }).catch(err => {
                    console.log(err)
                })
            },
            bofang(song){
                console.log(song)
             alert("暂无电台播放功能！")
            }
        }
    }
</script>

<style scoped>
    .gedan{
        width: 100%;
        margin: 10px auto;
        padding: 0;
        list-style: none;
        background: white;
    }
    .gelan{
        width: 95%;
        height: 100px;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        line-height: 50px;
        text-align: left;
        padding: 10px;
    }
    .mname{
        margin-left: 120px;
        top: -70px;
        position: relative;
    }
    .dtimg{
        width: 100px;
        height: 100px;
        cursor: pointer;
    }
    .dtimg:hover .but{
        color: #ff9812;
        opacity: 1;
    }
    .but{
        font-size: 50px;
        position: relative;
        top: -95px;
        left: 25px;
        color: white;
        opacity: 0.7;
    }
</style>

